/* animations! */
/* at-rules */
@keyframes circleGrows {
    0% {
        /* percent in time */
        opacity: 1;
        transform: scale(0.01);
        border-width: 2rem;
    }

    50% {
        opacity: 1;
        border-width: 0.8rem;
    }

    100% {
        opacity: 0;
        transform: scale(1);
        border-width: 0.4rem;
    }
}
